import React, { useState } from 'react'
import { Navbar,Nav,NavDropdown,Button,Jumbotron } from 'react-bootstrap';
import './App.css';
import Data from './data';
import Detail from './Detail';
import { Link, Route, Switch } from 'react-router-dom';
import axios from 'axios';


function App() {
  
  let [shoes, setShoes] = useState(Data)
  let [재고, 재고변경] = useState([10,11,12]);


  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Navbar.Brand href="/">React-Bootstrap</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link as={Link} to="/"> Home </Nav.Link>
            <Nav.Link as={Link} to="/detail/0"> Detail </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>

      <Switch>
        <Route exact path="/">
          <Jumbotron className="background">
            <h1>Hello, world!</h1>
            <p>
              This is a simple hero unit, a simple jumbotron-style component for calling
              extra attention to featured content or information.
            </p>
            <p>
              <Button variant="primary">Learn more</Button>
            </p>
          </Jumbotron>
          <div className="container">
            <div className="row">
              {
                shoes.map((row, i)=>{
                  return <Item key={i} param = {row} /> 
                })
              }
            </div>
            <button className="btn btn-primary" onClick={()=>{

              axios.get('https://codingapple1.github.io/shop/data2.json')
              .then((result)=>{
                var newA = [...shoes, ...result.data];
                setShoes(newA)

              }).catch(()=>{
                console.log('fail..')
              })

              }}>더보기</button>
          </div>
          
        </Route>
        <Route path="/detail/:id">
          <Detail param = {shoes} 재고 = {재고} 재고변경={재고변경} /> 
        </Route>
        <Route path="/:id">
            <div>새로 만든 route입니다</div>
        </Route>
      </Switch>

      

    </div>
  );
}

function Item(props){
  
  return(
    <>
      <div className="col-md-4">
        <img src={'https://codingapple1.github.io/shop/shoes' + (props.param.id+1) + '.jpg'} width="100%" alt={ props.param.id } />
        <h4>{ props.param.title }</h4>
        <p>{ props.param.content }</p>
        <Link to={"/detail/"+props.param.id}>상세보기</Link>
      </div>
    </>
  )
}



export default App;
