import React, { useState, useEffect } from 'react'
import {useHistory, useParams} from 'react-router-dom'
import './Detail.scss';



function Detail(props){

  var [alert, alertChange] = useState(true)
  let [inputData, inputDataChange] = useState('');

  // 컴포넌트 마운드, 업데이트 될때
  useEffect(()=>{
    var toggle = alert === true ? false : true 
    
    let timer = setTimeout(()=>{       
      // console.log('useEffect', toggle)
      alertChange(toggle)
    }, 2000);
    return () => clearTimeout(timer); // 컴포넌트 사라질때 실행 (언마운트)

  }, [])
  // []; 컴포넌트 로드할때 딱 한번 실행됨

  useEffect(()=>{
    console.log('useEffect2')
  }, [])

  let { id } = useParams();
  let history = useHistory()

  let item = props.param.find((row)=>{
    return row.id == id;
  })


  return(
    <>
      <div className="container">
      { inputData }
      <input onChange={ (e)=>{ inputDataChange(e.target.value) }}/>
      {
        alert === true ? <Alert /> : null
      }
        
        <div className="row">
          <div className="col-md-6">
            <img src={'https://codingapple1.github.io/shop/shoes' + (parseInt(id)+1) + '.jpg'} width="100%" />
          </div>
          <div className="col-md-6 mt-4">
            <h4 className="pt-5">{ item.title }</h4>
            <p>{ item.content }</p>
            <p>{ item.price }</p>
            <Info 재고={props.재고}></Info>
            <button className="btn btn-danger" onClick={()=>{ props.재고변경([9,10,11]) }}>주문하기</button> 
            <button className="btn btn-warning" 
            onClick={()=>{ 
              //   history.goBack();  
                history.push('/')
              }}
            >뒤로</button> 
          </div>
        </div>
      </div> 
      
    </>
  )
}


function Alert(){
  
  return(
    <>
      <div className="my-alert">
        <p>재고가 얼마 남지 않았습니다</p>
      </div>
    </>
  )
}

function Info(props){
  return <p>재고 : { props.재고[0] }</p>
}

export default Detail
  